import React from 'react';
import { ArticleHeader, MetaTags, MoreReading } from '_components';
import { Article } from '_types';
import './ArticleTemplate.scss';
import { PageContainer } from './PageContainer';

type ArticleTemplateProps = {
  article: Article;
};

const ArticleTemplate: React.FC<ArticleTemplateProps> = (props) => {



  return (
    <div className="ArticleTemplate">

      <MetaTags
        title={`${props.article.title} | romiem`}
        description={props.article.description}
        image={props.article.image}
      />

      <PageContainer>

        <ArticleHeader
          title={props.article.title}
          subtitle={props.article.description}
          date={props.article.date}
          image={props.article.image}
        />

        {props.children}

      </PageContainer>

      <MoreReading articles={[props.article, props.article, props.article, props.article]} />

    </div>
  );
};

export { ArticleTemplate };
