import React from 'react';
import { LazyImage } from '_components';
import './ArticleHeader.scss';

type ArticleHeaderProps = {
  title: string;
  subtitle: string;
  date: string;
  image: string;
};

const ArticleHeader: React.FC<ArticleHeaderProps> = (props) => {

  const formatDate = (date: string): string => {
    const dateObj = new Date(date);
    return dateObj.toLocaleDateString('en-gb', { day: 'numeric', month: 'long', year: 'numeric' });
  };

  return (
    <div className="ArticleHeader">
      <h1 className="ArticleHeader__title">{props.title}</h1>
      <span className="ArticleHeader__divider"></span>
      <p className="ArticleHeader__subtitle">{props.subtitle}</p>
      <p className="ArticleHeader__date">{formatDate(props.date)}</p>
      <div className="ArticleHeader__image">
        <LazyImage
          src={props.image}
          heightRatio={2 / 3}
        />
      </div>
    </div>
  );
};

export { ArticleHeader };
