import { create } from 'zustand';
import { analyticsService } from '_services';

export type AnalyticsStore = {
  isEnabled: boolean;
  enable: () => void;
  disable: () => void;
};

const [useAnalyticsStore] = create<AnalyticsStore>((set): AnalyticsStore => ({
  isEnabled: false,
  enable: () => {
    analyticsService.enable();
    return set(state => ({ isEnabled: true }))
  },
  disable: () => {
    analyticsService.disable();
    return set(state => ({ isEnabled: false }))
  },
}));

export { useAnalyticsStore };
