import React from 'react';
import { LazyImage } from '_components/LazyImage';
import './ArticleImageGrid.scss';

type ArticleImageGridItem = {
  image: string;
  heightRatio?: number;
  colSpan?: number;
};

type ArticleImageGridProps = {
  items: ArticleImageGridItem[] | ArticleImageGridItem;
  // Height ratio of each image in the array. Can be overridden on a per-image basis.
  heightRatio?: number;
  // Columns to span for each image in the array. Can be overridden on a per-image basis.
  colSpan?: number;
  caption?: string;
};

const ArticleImageGrid: React.FC<ArticleImageGridProps> = (props) => {

  const imageStyle = (item: ArticleImageGridItem): React.CSSProperties => ({
    gridColumn: `span ${item.colSpan || props.colSpan}`,
  });

  const images = Array.isArray(props.items) ? props.items : [props.items];

  return (
    <div className="ArticleImageGrid">
      <div className="ArticleImageGrid__grid">
        {images.map((item, idx) =>
          <div key={idx} className="ArticleImageGrid__image" style={imageStyle(item)}>
            <LazyImage src={item.image} heightRatio={item.heightRatio || props.heightRatio} />
          </div>
        )}
      </div>
      {props.caption &&
        <div className="ArticleImageGrid__caption">{props.caption}</div>
      }
    </div>
  );
};

ArticleImageGrid.defaultProps = {
  colSpan: 12,
};

export { ArticleImageGrid };
