import React from 'react';
import { PrismAsyncLight as SyntaxHighlighter } from 'react-syntax-highlighter';
import xonokai from 'react-syntax-highlighter/dist/esm/styles/prism/xonokai';
import './CodeSample.scss';

type CodeSampleProps = {
  code: string;
  language: string;
};

const CodeSample: React.FC<CodeSampleProps> = (props) => {

  return (
    <div className="CodeSample">
      <SyntaxHighlighter language={props.language} style={xonokai}>{props.code}</SyntaxHighlighter>
    </div>
  );
};

export { CodeSample };
