import {useState, useEffect} from 'react';

const useLoadImage = (src: string) => {

  const [loaded, setLoaded] = useState<boolean>(false);
  const [error, setError] = useState<null|string|Event>(null);
  const [cached, setCached] = useState<boolean>(false);

  useEffect(() => {
    const loadImage = (imgSrc: string) => {
      const image = new Image();
      image.onload = () => setLoaded(true);
      image.onerror = err => setError(err);
      image.src = imgSrc;
      if (image.complete || (image.width + image.height) > 0) {
        setCached(true);
      }
    };
    loadImage(src);
  }, [src]);

  return {
    loaded,
    error,
    cached
  };
};

export { useLoadImage };
