import React from 'react';
import TimeAgo from 'react-timeago';
import { ColumnLayout, LazyImage } from '_components';
import './HeroArticle.scss';
import { PageContainer } from './PageContainer';
import { Article } from '_types';
import { Link } from 'react-router-dom';

type HeroArticleProps = {
  article: Article;
};

const HeroArticle: React.FC<HeroArticleProps> = (props) => {

  return (
    <div className="HeroArticle">
      <PageContainer>
        <ColumnLayout className="HeroArticle__grid">
          <Link to={props.article.path} className="HeroArticle__image anchor anchor--noUnderline">
            <LazyImage
              src={props.article.image}
              previewSrc={props.article.image}
              heightRatio={2 / 3}
            />
          </Link>
          <div className="HeroArticle__text">
            <h1 className="HeroArticle__title">
              <Link to={props.article.path} className="anchor anchor--noUnderline">{props.article.title}</Link>
            </h1>
            <p className="HeroArticle__description">{props.article.description}</p>
            <p className="HeroArticle__timeAgo">
              <TimeAgo date={props.article.date} />
            </p>
          </div>
        </ColumnLayout>
      </PageContainer>
    </div>
  );
};

export { HeroArticle };
