import { useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { analyticsService } from '_services';

type TrackPageProps = {};

const TrackPage: React.FC<TrackPageProps> = (props) => {

  const location = useLocation();

  useEffect(() => {
    analyticsService.trackPage(location.pathname);
  }, [location.pathname]);

  return (null);
}

export { TrackPage };
