import React from 'react';
import classnames from 'classnames';
import { analyticsService } from '_services';

type ExternalLinkProps = {
  href: string;
  className?: string;
  [key: string]: any;
};

const ExternalLink: React.FC<ExternalLinkProps> = ({ href, className, children, ...restProps}) => {

  const handleLinkClick = (evt: React.MouseEvent<HTMLAnchorElement, MouseEvent>): void => {
    analyticsService.trackOutboundLink(evt.currentTarget.href);
  };

  const classes = classnames('ExternalLink', className);

  return (
    <a
      className={classes}
      href={href}
      onClick={handleLinkClick}
      target="_blank"
      rel="noopener noreferrer"
      {...restProps}
      >
        {children}
      </a>
  );
};

ExternalLink.defaultProps = {
  className: '',
}

export { ExternalLink };
