import ReactGA from 'react-ga';

const trackingId: string = process.env.REACT_APP_GOOGLE_ANALYTICS_ID || '';
let isEnabled = false;

const initialize = () => {
  if (isEnabled) return;
  ReactGA.initialize(trackingId);
};

const enable = () => {
  initialize();
  isEnabled = true;
};

const disable = () => {
  isEnabled = false;
};

const trackPage = (path: string) => {
  if (!isEnabled) return;
  ReactGA.pageview(path);
};

const trackOutboundLink = (url: string) => {
  if (!isEnabled) return;
  ReactGA.outboundLink({
    label: url
  }, () => { });
}

const trackEvent = (category: string, action: string, label: string) => {
  if (!isEnabled) return;
  ReactGA.event({
    category: category,
    action: action,
    label: label,
  });
}

export const analyticsService = {
  enable,
  disable,
  trackPage,
  trackOutboundLink,
  trackEvent,
};
