import React from 'react';
import classnames from 'classnames';
import './ColumnLayout.scss';

type ColumnLayoutProps = {
  className?: string;
};

const ColumnLayout: React.FC<ColumnLayoutProps> = (props) => {

  const classes = classnames('ColumnLayout', props.className);

  return (
    <div className={classes}>
      {props.children}
    </div>
  );
};

export { ColumnLayout };
