import React from 'react';
import classnames from 'classnames';
import './Page.scss';

type PageProps = {
  className?: string;
};

const Page: React.FC<PageProps> = (props) => {

  const classes = classnames('Page', 'a-routeFadeIn', props.className);

  return (
    <div className={classes}>
      {props.children}
    </div>
  );
};

export { Page };
