import React from 'react';
import TimeAgo from 'react-timeago';
import { ColumnLayout, LazyImage } from '_components';
import './MoreReading.scss';
import { PageContainer } from './PageContainer';
import { Article } from '_types';
import { Link, useLocation } from 'react-router-dom';
import { ARTICLES } from 'articles';

type MoreReadingProps = {
  articles: Article[];
};

const MoreReading: React.FC<MoreReadingProps> = (props) => {

  const location = useLocation();

  // Filter the current article out
  const articles = Object.values(ARTICLES).filter(item => item.path !== location.pathname);

  return (
    <div className="MoreReading">
      <h1 className="MoreReading__title">Recent posts</h1>
      <PageContainer>
        <ColumnLayout className="MoreReading__grid">
          {articles.map((item, idx) =>
            <Link className="MoreReading__card anchor anchor--noUnderline" key={idx} to={item.path}>
              <LazyImage
                src={item.image}
                previewSrc={item.image}
                heightRatio={2 / 3}
              />
              <h4 className="MoreReading__cardTitle">{item.title}</h4>
              <p className="MoreReading__cardTimeAgo">
                <TimeAgo date={item.date} />
              </p>
            </Link>
          )}
        </ColumnLayout>
      </PageContainer>
    </div>
  );
};

export { MoreReading };
