import React from 'react';
import TimeAgo from 'react-timeago';
import { ColumnLayout, LazyImage } from '_components';
import './SecondaryArticles.scss';
import { PageContainer } from './PageContainer';
import { Article } from '_types';
import { Link } from 'react-router-dom';

type SecondaryArticlesProps = {
  articles: Article[];
};

const SecondaryArticles: React.FC<SecondaryArticlesProps> = (props) => {

  const articles = props.articles.slice(0, 4);

  return (
    <div className="SecondaryArticles">
      <PageContainer>
        <ColumnLayout className="SecondaryArticles__grid">
          {articles.map((item, idx) =>
            <Link to={item.path} className="SecondaryArticles__card anchor anchor--noUnderline" key={idx}>
              <LazyImage
                src={item.image}
                previewSrc={item.image}
                heightRatio={2 / 3}
              />
              <h4 className="SecondaryArticles__cardTitle">{item.title}</h4>
              <p className="SecondaryArticles__cardTimeAgo">
                <TimeAgo date={item.date} />
              </p>
            </Link>
          )}
        </ColumnLayout>
      </PageContainer>
    </div>
  );
};

export { SecondaryArticles };
